body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html, body {
    margin: 0;
    padding: 0;
}

body {
    background-color:#323334;
}




.container {
    background-color: #323334;
    box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
    display: flex;
    font-weight: bold;
    height: 80px;
    justify-content: space-between;
    padding: 0 40px;
    width: 100%;
}

.header-color {
    background: #D31027; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #EA384D, #D31027); /* Chrome 10-25, Safari 5.1-6 */
}

.header {
    background: #323334;
    color: #FFF;
    font-weight: bold;
    font-size: 20px;
}

.cell {
    background: #323334;
    color: #ffe66d;
    font-weight: bold;
    font-size: 25px;
    margin-top: 30px;
}

.hide {
    display: none;

}

.menu {
    background-color: #323334;
    box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
    display: flex;
    height: 80px;
    justify-content: space-between;
    padding: 0 40px;
    position: relative;
}

.menu__right {
    align-items: center;
    display: flex;
    height: 100%;
}

.menu__list {
    height: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.menu__list-item {
    display: flex;
    height: 100%;
    margin-right: 20px;
}

.menu__list-item:last-child {
    margin-right: 0;
}

.menu__link {
    align-items: center;
    color: #FFF;
    display: flex;
    font-size: 16px;
    height: 100%;
    padding: 0 3px;
    text-decoration: none;
    text-transform: uppercase;
}

.menu__link {
    align-items: center;
    color: #FFF;
    display: flex;
    font-size: 16px;
    height: 100%;
    padding: 0 3px;
    text-decoration: none;
    text-transform: uppercase;
}

.menu__link:hover {
    color: #ffa69e;
}

.menu__link--active {
    border-top: 4px solid #ffa69e;
    color: #ffa69e;
    font-weight: 700;
}

.title {
    color: white;
    font-family: 'Anton';
    font-size: 50px;
    margin-bottom: 40px;
}

.avatarImg {
    border: 3px solid;
    border-radius: 50%;
    height: 150px;
    margin-top: 6em;

}

.bannerTextHr {
    border-top: 5px dotted  #bcbcbc;
    margin: auto;
    width: 50%;
}

.bannerTextHone{
    color: white;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 0.5em;
}

.bannerTextP{
    color: white;
    flex: 1 1;
    font-size: 15px;
    padding: 1em;
    margin-top: 1em;
}

.bannerText {
    border-radius: 10px;
    margin-top: 3em;
    opacity: 0.8;
    width: 100%;
}

.landingGrid {
    background: #323334;
    border-top: 5px solid #ffe66d;
    flex-direction: column;
    height: 100%;
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
}

.mediumLogo {
    color: #ffe66d;
    height: 45px;
    margin-top: 14px;
    width: 45px;

}

.alfr {
    color: #ffe66d;
    height: 175px;
    width: 345px;
}

.socialLinks {
    display: flex;
    justify-content: space-evenly;
    margin-top: 5em;
}

@media (min-width: 400px){
    .avatarImg {
        border: 3px solid;
        border-radius: 50%;
        flex: 1 1;
        height: 200px;
        margin-top: 6em;
        text-align: center;

    }
    .bannerTextHone{
        color: white;
        flex: 1 1;
        font-size: 50px;
        font-weight: bold;
    }
    .bannerTextP{
        color: white;
        flex: 1 1;
        font-size: 17px;
        padding: 1em;
    }
    .item{
        color: white;
        font-size: 20px;
        font-family: "Anton";
    }
}
@media (min-width: 800px){
    .avatarImg {
        border-radius: 50%;
        border: 3px solid #FFF;
        flex: 1 1;
        height: 250px;
        margin-top: 6em;

    }
    .bannerTextHone{
        color: white;
        flex: 1 1;
        font-size: 60px;
        font-weight: bold;
    }
    .bannerTextP{
        color: white;
        flex: 1 1;
        font-size: 20px;
        padding: 1em;
    }
    .item{
        color: white;
        font-size: 33px;
        font-family: "Anton";
        margin-bottom: 15px;
    }
}
.contactBody {
    background: #323334;
    border-top: 5px solid #9fc5ff;
    height: 100%;
    justify-content: center;
    margin: auto;
    padding-top: 2em;
    width: 100%;
}

.hr {
    border-top: 5px dotted #9fc5ff;
    margin: auto auto 20px auto;
    width: 50%;

}

.title {
    font-family: Anton;
    font-size: 48px;
    color: #FFF;
    margin-bottom: 20px;
}

.pgraph {
    font-family: Oxygen;
    font-weight: bold;
    margin: auto;
    padding-top: 1em;
    width: 75%;
}

.buttonCard {
    color: #000;
    float: right;
}

.card {
    height: 400px;
    max-width: 345px;
}

.cardEvodeck {
    height: 285px;
    width: 345px;
}

.cardMedia {
    background: black center;
    background-size: contain;
    height: 120px;
}
.cardMediaEvodeck {
    background: #f2ffe6 center;
    background-size: contain;
    height: 120px;
}

.categoryTabs {
    background: #323334;
    height: 100%;
    margin: auto;
    position: fixed;
    text-align: center;
    width: 100%;
}
.mainTab {
    background: #FFF;
    border-top: 5px solid #ffa69e;
    border-bottom: 6px solid #ffa69e;
}

.media {
    height: 140px;
}

.projectsGrid {
    display: flex;
    justify-content: space-evenly;
    margin-top: 30px;
}


